<template>
    <div class='proList'>
        <div class="content">
            <div class="left">
                <div class="title">
                    <div></div>
                    <div>{{$t('list[1]')}}</div>
                    <div></div>
                </div>
                <div class="typeList" v-for="(item,i) in category_list" :key="i">
                    <el-collapse v-model="activeNames" @change="handleChange">
                        <el-collapse-item class="yiji" :title="item.category_name" :name="item.id">
                            <div v-for="(item1,j) in item.child" :key="j">
                                <el-collapse-item class="erji" :title="item1.category_name" :name="item1.id">
                                    <div class="sanji" :class="{checked:ageActive == item2.id}" v-for="(item2,z) in item1.child" :key="z" @click="getList(item2.id)">
                                        {{item2.category_name}}
                                    </div>
                                </el-collapse-item>
                            </div>
                        </el-collapse-item>
                    </el-collapse>
                </div>
            </div>
            <div class="right">
                <div class="rank">
                    <div :class="{checked:ageActive1 == 1}" @click="paixu(1)">{{$t('list[2]')}}↓</div>
                    <div :class="{checked:ageActive1 == 2}" @click="paixu(2)">{{$t('list[3]')}}↓</div>
                    <div :class="{checked:ageActive1 == 3}" @click="paixu(3)">{{$t('list[4]')}}↓</div>
                </div>
                <div class="proList">
                    <div class="zanwu" v-if="show">
                        <img src="../../assets/img/shangcheng/tubiao.png" alt="">
                        <h1>{{$t('list[5]')}}...</h1>
                    </div>
                    <div v-else class="listItem" @click="xianging(item.id)" v-for="(item,i) in get_goods_list" :key="i">
                        <div class="pro">
                            <div class="img"><img :src="imgUrl + item.thumb" alt=""></div>
                            <h1>{{item.name}}</h1>
                            <div class="price">
                                <div> <span>￥</span> <span>{{item.sell_price}}</span> </div>
                                <div>￥{{item.market_price}}</div>
                                <div>{{$t('list[6]')}}{{item.sell_num}}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="fenye">
                    <div>
                        1-{{pageNumber}} / {{$t('list[8]')}}{{total}}{{$t('list[9]')}}
                    </div>
                    <div>
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page.sync="currentPage3"
                            :page-size="pageNumber"
                            layout="prev, pager, next, jumper"
                            :total="total">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <div class="zhaohuoBtn" @click="$router.push({path:'/zhaohuo',query:{title:$t('user[7]')}})">
            <img src="../../assets/img/shangcheng/fabuxuqiu.png" alt="111">
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            ageActive:'',//选中状态
            ageActive1:'',//排序方式选中
            category_list:'',//三级分类
            activeNames:'',//分类选中id
            option1:'',
            option2:'',
            option3:'',
            value1: '',
            value2: '',
            value3: '',
            show:'',//是否有商品
            list:false,
            currentPage3: 1,
            key:'',//搜索的关键词参数
            page:1,//页数
            pageNumber:12,//每页数量
            total:0,//总数
            get_goods_list:'',//搜索商品列表
            var:'',//参数列表
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        if(_this.$route.query.key){
            _this.key = _this.$route.query.key
            _this.var = {
                lang:_this.apis.lang,
                status:1,
                key:_this.$route.query.key,
                category_id:'',
                page:_this.page,
                pageNumber:_this.pageNumber,
            }
            _this.apis.get(_this.apis.get_goods_list,{params:_this.var}).then(function(res) {
                if(res.data.code == 0){
                    _this.get_goods_list = res.data.data.data
                    _this.total = res.data.data.total
                    if(res.data.data.data.length>=1){
                        _this.show = false
                    }else{
                       _this.show = true 
                    }
                }
            })
        }else{
            _this.var = {
                lang:_this.apis.lang,
                status:1,
                key:'',
                category_id:'',
                pageNumber:_this.pageNumber,
            }
            _this.apis.get(_this.apis.get_goods_list,{params:_this.var}).then(function(res) {
                if(res.data.code == 0){
                    _this.get_goods_list = res.data.data.data
                    _this.total = res.data.data.total
                    if(res.data.data.data.length>=1){
                        _this.show = false
                    }else{
                       _this.show = true 
                    }
                }
            })
        }
        //获取一级分类
        _this.apis.get(_this.apis.get_first_category,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.option1 = res.data.data
            }
        })
        //获取三级分类
        _this.apis.get(_this.apis.category_list,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.category_list = res.data.data
            }
        })
    },
    watch:{
    },
    methods:{
        //排序方式
        paixu(i){
            var _this = this;
            _this.ageActive1 = i
            _this.var.status = i
            _this.apis.get(_this.apis.get_goods_list,{params:_this.var}).then(function(res) {
                if(res.data.code == 0){
                    _this.get_goods_list = res.data.data.data
                    _this.total = res.data.data.total
                    if(res.data.data.data.length>=1){
                        _this.show = false
                    }else{
                       _this.show = true 
                    }
                }
            })
        },
        //获取商品列表
        getList(id){
            var _this = this
            if(_this.$route.query.key){
                _this.$router.push({
                    path:'bai',
                    query:{title:_this.$t('list[0]'),id:id}
                })
            }
            _this.ageActive = id
            _this.var.category_id = id,
            _this.apis.get(_this.apis.get_goods_list,{params:_this.var}).then(function(res) {
                if(res.data.code == 0){
                    _this.get_goods_list = res.data.data.data
                    _this.total = res.data.data.total
                    if(res.data.data.data.length>=1){
                        _this.show = false
                    }else{
                       _this.show = true 
                    }
                }
            })
        },
        //选中分类事件
        handleChange(){
            var _this = this;
            console.log(_this.activeNames)
        },
         xianging(id){
            var _this = this;
            _this.$router.push({
                path:'xiangqing',
                query:{title:_this.$t('list[0]'),id:id}
            })
        },
        handleCurrentChange(val) {
            var _this = this
            _this.var.page = val
            _this.apis.get(_this.apis.get_goods_list,{params:_this.var}).then(function(res) {
                if(res.data.code == 0){
                    _this.get_goods_list = res.data.data.data
                    _this.total = res.data.data.total
                    if(res.data.data.data.length>=1){
                        _this.show = false
                    }else{
                       _this.show = true 
                    }
                }
            })
        },
        handleSizeChange(){
        },
        select1(){
            var _this = this;
            _this.option2 = ''
            _this.value2 = ''
            _this.option3 = ''
            _this.value3 = ''
            _this.apis.get(_this.apis.get_next_category,{params:{lang:_this.apis.lang,id:_this.value1}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.option2 = res.data.data
                }
            })
        },
        select2(){
            var _this = this;
            _this.option3 = ''
            _this.value3 = ''
            _this.apis.get(_this.apis.get_next_category,{params:{lang:_this.apis.lang,id:_this.value2}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.option3 = res.data.data
                }
            })
        },
        select3(){
            var _this = this;
            _this.var = {
                lang:_this.apis.lang,
                status:1,
                key:'',
                pageNumber:_this.pageNumber,
                category_id:_this.value3
            }
            _this.apis.get(_this.apis.get_goods_list,{params:_this.var}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.get_goods_list = res.data.data.data
                    _this.total = res.data.data.total
                    if(res.data.data.data.length>=1){
                        _this.show = false
                    }else{
                       _this.show = true 
                       console.log(111)
                    }
                }
            })
        },
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.content{
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    .left{
        width: 200px;
        margin-right: 18px;
        padding-top: 20px;
        .el-collapse-item__header{
            height: 58px;
        }
        .title{
            display:flex;
            justify-content: CENTER;
            align-items: center;
            border-bottom: 1px solid #FFAD01;
            padding-bottom: 16px;
            &>div:nth-child(1){
                width: 20px;
                height: 1px;
                background: #FFAD00;
            }
            &>div:nth-child(2){
                color: #FF6600;
                font-size: 20px;
                margin: 0 10PX;
            }
            &>div:nth-child(3){
                width: 20px;
                height: 1px;
                background: #FFAD00;
            }
        }
        .typeList{
            font-size: 16px;
            .checked{
                color: #FF6600;
            }
            .sanji:hover{
                color: #FF6600;
            }
            .sanji{
                cursor:pointer;
            }
        }
    }
    .right{
        flex: 1;
        .type{
            color: #000;
            &>div:last-child{
                border: none;
            }
            .typeItem{
                font-size: 14px;
                font-weight: 700;
                height: 43px;
                line-height: 43px;
                display: flex;
                align-items: center;
                border-bottom: 1px solid #E5E5E5;
                &>div:nth-child(1){
                    width: 70px;
                    color: #838383;
                    cursor:pointer;
                }
                &>div:nth-child(2){
                    flex: 1;
                }
                .typeRight1{
                    color: #666666;
                    display: flex;
                    justify-content: left;
                    align-items: center;
                    div{
                        height: 24px;
                        line-height: 24px;
                        margin: 0 7px;
                        border: 1px solid #E1E1E1;
                        padding-left: 10px;
                        padding-right: 25px;
                        position: relative;
                        cursor:pointer;
                        span{
                            display: inline-block;
                            width: 15px;
                            height: 15px;
                            background: #000;
                            position: absolute;
                            top: 50%;
                            right: 5px;
                            transform: translateY(-50%);
                        }
                    }
                }
                .typeRight2{
                    color: #2C2C2C;
                    display: flex;
                    justify-content: left;
                    align-items: center;
                    &>div{
                        height: 24px;
                        line-height: 24px;
                        margin: 0 4px;
                        padding: 0 10px;
                        position: relative;
                        cursor:pointer;
                    }
                    .select{
                        width: 136px;
                        height: 34px;
                        padding-left: 0;
                    }
                }
            }
        }
        .rank{
            width: 100%;
            height: 34px;
            line-height: 34px;
            font-size: 14px;
            color: #545454;
            margin: 20px auto;
            padding: 0 20px;
            display: flex;
            justify-content: left;
            align-items: center;
            background: #EBEBEB;
            .checked{
                color: #FF6600;
                // font-weight: bold;
            }
            div{
                margin-right: 20px;
                cursor:pointer;
            }
        }
        .proList{
            margin: 80px 0;
            margin-top: 20px;
            margin-bottom: 20px;
            .listItem{
                display: inline-block;
                margin-bottom: 30px;
                margin-right: 18px;
                border: 1px solid rgba(0, 0, 0, 0);
                overflow: hidden;
                .pro{
                    width: 230px;
                    height: 372px;
                    .img{
                        width: 230px;
                        height: 285px;
                        overflow: hidden;
                    }
                    h1{
                        font-size: 14px;
                        color: #565656;
                        text-align: left;
                        margin-top: 5px;
                        -webkit-line-clamp:2;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                    p{
                        font-size: 12px;
                        color: #959595;
                        text-align: center;
                    }
                    .price{
                        margin-top: 5px;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        line-height: 36px;
                        color: #9C9C9C;
                        &>div:nth-child(1){
                            color: #FF5A00;
                            &>span:nth-child(1){
                                font-size: 14px;
                            }
                            &>span:nth-child(2){
                                font-size: 16px;
                                font-weight: blod;
                            }
                        }
                        &>div:nth-child(2){
                            flex: 1;
                            margin-left: 10px;
                            text-decoration:line-through
                        }
                        &>div:nth-child(3){
                            font-weight: bold;
                        }
                    }
                }
            }
            &>.listItem:nth-child(4n){
                margin-right: 0;
            }
            .listItem:hover .img img{
                transform: scale(1.2);
                transition: 0.5s;
            }
            .listItem:hover{
                // border: 1px solid #FFAD00;
            }
            .zanwu{
                margin: 0 auto;
                text-align: center;
                img{
                    width: 338px;
                    height: 306px;
                }
                h1{
                    color: #9e9e9e;
                    font-size: 22px;
                    line-height: 50px; 
                    margin: 10px 0;
                }
            }
        }
        .fenye{
            margin-bottom: 80px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;
            color: #A09F9F;
            font-weight: bold;
            &>div{
                display: inline-block;
            }
            .active{
                color: red !important;
            }
        }
    }
}
.zhaohuoBtn{
    position: fixed;
    bottom: 50px;
    right: 50px;
    cursor:pointer;
}
</style>


